.speechWrapper {
    display: flex;
}

.message1 {
    background: whitesmoke;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    padding: 10px;
    margin: 20px;
    border-radius: 30px;
    max-width: 300px;
    min-width: 150px;
}

.bubble {
    width: 240px;
    height: 40px;
    display: block;
    background: #f5f5f5;
    border-radius: 4px;
    box-shadow: 2px 8px 5px #000;
    position: relative;
    margin: 0 0 25px;}

.alt {
    margin: 0 0 0 60px;
}

.txt {
    padding: 8px 55px 8px 14px;}

.name {
    font-weight: 600;
    font-size: 12px;
    margin: 0 0 4px;
    color: #3498db;}

span {
    font-weight: normal;
    color: #b3b3b3;
}

span.alt {
    color: #2ecc71;
}

.message {
    font-size: 12px;
    margin: 0;
    color: #2b2b2b;
}

.timestamp {
    font-size: 11px;
    position: absolute;
    bottom: 8px;
    right: 10px;
    text-transform: uppercase;
    color: #999
}

.bubbleArrow {
    position: absolute;
    width: 0;
    bottom: 42px;
    left: -16px;
    height: 0;
}
.bubbleArrow.alt {
    right: -2px;
    bottom: 40px;
    left: auto;
}

.bubbleArrow:after {
    content: "";
    position: absolute;
    border: 0 solid transparent;
    border-top: 9px solid #f5f5f5;
    border-radius: 0 20px 0;
    width: 15px;
    height: 30px;
    transform: rotate(145deg);
}

.bubbleArrow.alt:after {
    transform: rotate(145deg) scaleY(-1);
}

.inputt {
    width: 240px;
    height: 30px;
    border-radius: 10px;
    box-shadow: 2px 8px 5px #000;
    background: rgba(34, 101, 163, 0.5)
}

.inputt:hover{
    border: 2px solid rgba(34, 101, 163, 0.5);
    background: white;
}

.inputt:focus{
    border: 2px solid rgba(34, 101, 163, 0.5);
    background: white;
}

.button {
        margin: 15px;
}